.App {
  min-height: 100vh;
  min-width: fit-content;
  overflow-x: auto;
}
.form {
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
}
.form input {
  padding: 0.5rem;
  margin: 0.5rem;
}
table {
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%;
}
td {
  margin: 0;
  width: 60px;
  padding: 5px;
  border: 1px solid black;
  text-align: center;
}
.tableCell {
  background-color: #a2a9af;
  position: relative;
}
.addBtn {
  background-color: #96ceb4;
}
.delBtn {
  background-color: #d9534f;
}
.percent {
  background-color: rgba(221,10,53,0.5);
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}
.closeValue {
  background-color: #f4f0e6;
}
.tableCell:hover {
  background-color: #d9d9f3;
}